/* Dashboard Buttons Overall Styling */

.btnSilver, .btnGold {
 	  background: #1e1e1e;
	  border: 2px solid transparent;
    color:#fff;
    max-width:150px;
    -o-transition: all .3s ease-in;
    -webkit-transition: all .3s ease-in;
    transition: all .3s ease-in;

    font-family: 'Droid Sans', sans-serif;

}


.btnGoldExp {
 	  background: #1e1e1e;
	  border: 2px solid transparent;
    color:#fff;
    -o-transition: all .3s ease-in;
    -webkit-transition: all .3s ease-in;
    transition: all .3s ease-in;

    font-family: 'Droid Sans', sans-serif;

}


.btnSilver h5, .btnGold h5, .btnGoldExp h5 {
  padding-left:5px;
}
.btnGold:hover, .btnGoldExp:hover {
    cursor:pointer;
    background: #b58e12;

}

.btnSilver:hover {
    cursor:pointer;
    background: #7f7f7f;
}

.btnSilver h5, .btnGold h5, .btnGoldExp h5 {
  margin:10px;
  margin-left:15px;
}


/* Manage Project Button (silver) */

.btnSilver{
	  -o-border-image: -o-linear-gradient(left, #7f7f7f 50%, #1e1e1e 100%);
	     border-image: -webkit-gradient(linear, left top, right top, color-stop(50%, #7f7f7f), to(#1e1e1e));
	     border-image: linear-gradient(to right, #7f7f7f 50%, #1e1e1e 100%);
	  -webkit-border-image: -webkit-linear-gradient(to right, #7f7f7f 50%, #1e1e1e 100%);
    border-image-slice: 1;
  }

/* Create Project Button (Gold) */

.btnGold, .btnGoldExp {
	  -o-border-image: -o-linear-gradient(left, #b58e12 50%, #1e1e1e 100%);
	     border-image: -webkit-gradient(linear, left top, right top, color-stop(50%, #b58e12), to(#1e1e1e));
	     border-image: linear-gradient(to right, #b58e12 50%, #1e1e1e 100%);
	  -webkit-border-image: -webkit-linear-gradient(to right, #b58e12 50%, #1e1e1e 100%);
    border-image-slice: 1;
}



