/* SVG Styling */


@-webkit-keyframes neon1 {
  from {
    text-shadow: 0 0 10px #ffb300, 0 0 20px #ffb300, 0 0 30px #ffb300, 0 0 40px , 0 0 70px #b58e12, 0 0 80px #b58e12, 0 0 100px #b58e12, 0 0 150px #b58e12;
  }
}


@keyframes neon1 {
  from {
    text-shadow: 0 0 10px #ffb300, 0 0 20px #ffb300, 0 0 30px #ffb300, 0 0 40px , 0 0 70px #b58e12, 0 0 80px #b58e12, 0 0 100px #b58e12, 0 0 150px #b58e12;
  }
}

@-webkit-keyframes neon4 {
  from {
    text-shadow: 0 0 10px #ffb300, 0 0 20px #ffb300, 0 0 30px #ffb300, 0 0 40px #b58e12, 0 0 70px #b58e12, 0 0 80px #b58e12, 0 0 100px #b58e12, 0 0 150px #b58e12;
  }
  to {
    text-shadow: 0 0 5px #ffb300, 0 0 10px #ffb300, 0 0 15px #ffb300, 0 0 20px #b58e12, 0 0 35px #b58e12, 0 0 40px #b58e12, 0 0 50px #b58e12, 0 0 75px #b58e12;
  }
}

@keyframes neon4 {
  from {
    text-shadow: 0 0 10px #ffb300, 0 0 20px #ffb300, 0 0 30px #ffb300, 0 0 40px #b58e12, 0 0 70px #b58e12, 0 0 80px #b58e12, 0 0 100px #b58e12, 0 0 150px #b58e12;
  }
  to {
    text-shadow: 0 0 5px #ffb300, 0 0 10px #ffb300, 0 0 15px #ffb300, 0 0 20px #b58e12, 0 0 35px #b58e12, 0 0 40px #b58e12, 0 0 50px #b58e12, 0 0 75px #b58e12;
  }
}

